export interface Profile {
  id: string
  name: string
  age: number
  description: string
  imageUrl: string
  interests: string[]
}

export interface ChatMessage {
  id: string
  senderId: string
  receiverId: string
  message: string
  timestamp: Date
}

export interface Match {
  id: string
  userId: string
  name: string
  imageUrl: string
  lastMessage?: string
  lastMessageTime?: Date
}

