'use client'

import { Match, formatMessageTime } from '@/lib/data'

interface ChatSidebarProps {
  matches: Match[]
  selectedMatchId: string | null
  onSelectMatch: (matchId: string) => void
}

export function ChatSidebar({ matches, selectedMatchId, onSelectMatch }: ChatSidebarProps) {
  return (
    <div className="w-full md:w-80 border-r bg-background">
      <div className="p-4 border-b">
        <h2 className="text-lg font-semibold">Your Matches</h2>
        <p className="text-sm text-muted-foreground">
          {matches.length} {matches.length === 1 ? 'match' : 'matches'}
        </p>
      </div>
      
      <div className="overflow-y-auto h-full">
        {matches.length === 0 ? (
          <div className="p-6 text-center">
            <div className="text-4xl mb-2">💬</div>
            <p className="text-muted-foreground">No matches yet</p>
            <p className="text-sm text-muted-foreground mt-1">
              Start swiping to find your matches!
            </p>
          </div>
        ) : (
          <div className="space-y-1">
            {matches.map((match) => (
              <button
                key={match.id}
                onClick={() => onSelectMatch(match.id)}
                className={`w-full p-4 text-left hover:bg-muted/50 transition-colors ${
                  selectedMatchId === match.id ? 'bg-muted' : ''
                }`}
              >
                <div className="flex items-center space-x-3">
                  <div className="relative">
                    <img
                      src={match.imageUrl}
                      alt={match.name}
                      className="w-12 h-12 rounded-full object-cover"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement
                        target.src = 'https://images.unsplash.com/photo-1494790108755-2616b612b786?w=400&h=400&fit=crop&crop=face'
                      }}
                    />
                    <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-background"></div>
                  </div>
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between">
                      <h3 className="font-medium truncate">{match.name}</h3>
                      {match.lastMessageTime && (
                        <span className="text-xs text-muted-foreground">
                          {formatMessageTime(match.lastMessageTime)}
                        </span>
                      )}
                    </div>
                    
                    {match.lastMessage && (
                      <p className="text-sm text-muted-foreground truncate mt-1">
                        {match.lastMessage}
                      </p>
                    )}
                  </div>
                </div>
              </button>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
