'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { ChatSidebar } from '@/components/ChatSidebar'
import { ChatWindow } from '@/components/ChatWindow'
import { useAuth } from '@/context/AuthContext'
import { dummyMatches, Match } from '@/lib/data'

export default function ChatPage() {
  const { user, logout } = useAuth()
  const router = useRouter()
  const [matches, setMatches] = useState<Match[]>([])
  const [selectedMatchId, setSelectedMatchId] = useState<string | null>(null)
  const [isMobileView, setIsMobileView] = useState(false)

  useEffect(() => {
    if (!user) {
      router.push('/login')
      return
    }
    
    // Load matches (in a real app, this would be fetched from an API)
    setMatches(dummyMatches)
    
    // Auto-select first match if available
    if (dummyMatches.length > 0) {
      setSelectedMatchId(dummyMatches[0].id)
    }
  }, [user, router])

  useEffect(() => {
    const checkMobile = () => {
      setIsMobileView(window.innerWidth < 768)
    }
    
    checkMobile()
    window.addEventListener('resize', checkMobile)
    
    return () => window.removeEventListener('resize', checkMobile)
  }, [])

  const handleLogout = () => {
    logout()
    router.push('/')
  }

  const selectedMatch = matches.find(match => match.id === selectedMatchId) || null

  if (!user) {
    return null
  }

  return (
    <div className="h-screen flex flex-col">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b bg-background">
        <div className="flex items-center space-x-4">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => router.push('/home')}
          >
            ← Back
          </Button>
          <div>
            <h1 className="text-xl font-bold">Messages</h1>
            <p className="text-sm text-muted-foreground">Stay connected with your matches</p>
          </div>
        </div>
        <div className="flex gap-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => router.push('/profile')}
          >
            Profile
          </Button>
          <Button
            variant="ghost"
            size="sm"
            onClick={handleLogout}
          >
            Logout
          </Button>
        </div>
      </div>

      {/* Main Chat Interface */}
      <div className="flex-1 flex overflow-hidden">
        {/* Mobile View */}
        {isMobileView ? (
          <div className="w-full">
            {!selectedMatchId ? (
              <ChatSidebar
                matches={matches}
                selectedMatchId={selectedMatchId}
                onSelectMatch={setSelectedMatchId}
              />
            ) : (
              <div className="h-full flex flex-col">
                <div className="p-2 border-b">
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setSelectedMatchId(null)}
                  >
                    ← Back to matches
                  </Button>
                </div>
                <div className="flex-1">
                  <ChatWindow
                    selectedMatch={selectedMatch}
                    currentUserId={user.id}
                  />
                </div>
              </div>
            )}
          </div>
        ) : (
          /* Desktop View */
          <>
            <ChatSidebar
              matches={matches}
              selectedMatchId={selectedMatchId}
              onSelectMatch={setSelectedMatchId}
            />
            <ChatWindow
              selectedMatch={selectedMatch}
              currentUserId={user.id}
            />
          </>
        )}
      </div>
    </div>
  )
}
