'use client'

import { useState, useRef, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { ChatMessage, getMessagesForConversation, Match } from '@/lib/data'

interface ChatWindowProps {
  selectedMatch: Match | null
  currentUserId: string
}

export function ChatWindow({ selectedMatch, currentUserId }: ChatWindowProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [newMessage, setNewMessage] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (selectedMatch) {
      const conversationMessages = getMessagesForConversation(currentUserId, selectedMatch.userId)
      setMessages(conversationMessages)
    }
  }, [selectedMatch, currentUserId])

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newMessage.trim() || !selectedMatch || isLoading) return

    setIsLoading(true)
    
    try {
      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 500))
      
      const message: ChatMessage = {
        id: Date.now().toString(),
        senderId: currentUserId,
        receiverId: selectedMatch.userId,
        message: newMessage.trim(),
        timestamp: new Date()
      }
      
      setMessages(prev => [...prev, message])
      setNewMessage('')
      
      // Simulate a response after a delay
      setTimeout(() => {
        const responses = [
          "That sounds great!",
          "I'd love to hear more about that.",
          "Absolutely! When are you free?",
          "That's so interesting!",
          "I completely agree with you.",
          "Thanks for sharing that with me.",
          "You always have the best ideas!",
          "I'm looking forward to it!"
        ]
        
        const randomResponse = responses[Math.floor(Math.random() * responses.length)]
        
        const responseMessage: ChatMessage = {
          id: (Date.now() + 1).toString(),
          senderId: selectedMatch.userId,
          receiverId: currentUserId,
          message: randomResponse,
          timestamp: new Date()
        }
        
        setMessages(prev => [...prev, responseMessage])
      }, 1000 + Math.random() * 2000)
      
    } catch (error) {
      console.error('Failed to send message:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
  }

  if (!selectedMatch) {
    return (
      <div className="flex-1 flex items-center justify-center bg-muted/20">
        <div className="text-center space-y-2">
          <div className="text-4xl">💬</div>
          <h3 className="text-lg font-medium">Select a match to start chatting</h3>
          <p className="text-sm text-muted-foreground">
            Choose someone from your matches to begin a conversation
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="flex-1 flex flex-col h-full">
      {/* Chat Header */}
      <div className="p-4 border-b bg-background">
        <div className="flex items-center space-x-3">
          <img
            src={selectedMatch.imageUrl}
            alt={selectedMatch.name}
            className="w-10 h-10 rounded-full object-cover"
            onError={(e) => {
              const target = e.target as HTMLImageElement
              target.src = 'https://images.unsplash.com/photo-1494790108755-2616b612b786?w=400&h=400&fit=crop&crop=face'
            }}
          />
          <div>
            <h3 className="font-medium">{selectedMatch.name}</h3>
            <p className="text-sm text-green-600">Online</p>
          </div>
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.length === 0 ? (
          <div className="text-center py-8">
            <p className="text-muted-foreground">
              Start your conversation with {selectedMatch.name}
            </p>
          </div>
        ) : (
          messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${
                message.senderId === currentUserId ? 'justify-end' : 'justify-start'
              }`}
            >
              <div
                className={`max-w-xs lg:max-w-md px-4 py-2 rounded-2xl ${
                  message.senderId === currentUserId
                    ? 'bg-primary text-primary-foreground'
                    : 'bg-muted'
                }`}
              >
                <p className="text-sm">{message.message}</p>
                <p
                  className={`text-xs mt-1 ${
                    message.senderId === currentUserId
                      ? 'text-primary-foreground/70'
                      : 'text-muted-foreground'
                  }`}
                >
                  {formatTime(message.timestamp)}
                </p>
              </div>
            </div>
          ))
        )}
        <div ref={messagesEndRef} />
      </div>

      {/* Message Input */}
      <div className="p-4 border-t bg-background">
        <form onSubmit={handleSendMessage} className="flex space-x-2">
          <Input
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            placeholder={`Message ${selectedMatch.name}...`}
            disabled={isLoading}
            className="flex-1"
          />
          <Button 
            type="submit" 
            disabled={!newMessage.trim() || isLoading}
            className="px-6"
          >
            {isLoading ? 'Sending...' : 'Send'}
          </Button>
        </form>
      </div>
    </div>
  )
}
